<template>
  <div class="fondo animacion" :class="cambiarFondo">
    <div class="container">
      <div id="Me">

        <!-- Información principal -->
        <div class="me-info">
          <p class="saludo">Hola a todos!</p>
          <h1 class="nombre">Soy Quique Velasco</h1>
          <p class="giro">Desarrollador web y programador</p>
          <div class="menu">
            <div class="puntos">
              <div class="punto" :class="{'indicador-active': option1, 'indicador-no-active' :!option1}"></div>
              <div class="punto" :class="{'indicador-active': option2, 'indicador-no-active' :!option2}"></div>
              <div class="punto" :class="{'indicador-active': option3, 'indicador-no-active' :!option3}"></div>
              <div class="punto" :class="{'indicador-active': option4, 'indicador-no-active' :!option4}"></div>
              <div class="punto" :class="{'indicador-active': option5, 'indicador-no-active' :!option5}"></div>
            </div>
            <div class="opciones">
              <router-link :to="{ name: 'About'}" style="text-decoration: none">
                <div class="opcion" :class="{'option-active': option1, 'option-no-active' :!option1}" to>
                  <p>Más sobre mi</p>
                  <div class="icon">
                    <font-awesome-icon icon="eye" style="font-size:22px; color:var(--colorGris1)"/>
                  </div>
                </div></router-link>
              <router-link :to="{ name: 'Services'}" style="text-decoration: none">
                <div class="opcion" :class="{'option-active': option2, 'option-no-active' :!option2}">
                  <p>Mis servicios</p>
                  <div class="icon">
                    <font-awesome-icon icon="eye" style="font-size:22px; color:var(--colorGris1)"/>
                  </div>
                </div>
              </router-link>
              <router-link :to="{ name: 'LastProjects'}" style="text-decoration: none">
                <div class="opcion" :class="{'option-active': option3, 'option-no-active' :!option3}">
                  <p>Últimos trabajos</p>
                  <div class="icon">
                    <font-awesome-icon icon="eye" style="font-size:22px; color:var(--colorGris1)"/>
                  </div>
                </div>
              </router-link>
              <router-link :to="{ name: 'Clients'}" style="text-decoration: none">
                <div class="opcion" :class="{'option-active': option4, 'option-no-active' :!option4}">
                  <p>Referencias de mis clientes</p>
                  <div class="icon">
                    <font-awesome-icon icon="eye" style="font-size:22px; color:var(--colorGris1)"/>
                  </div>
                </div>
              </router-link>
              <router-link :to="{ name: 'Contact'}" style="text-decoration: none">
                <div class="opcion" :class="{'option-active': option5, 'option-no-active' :!option5}">
                    <p>Contáctame</p>
                    <div class="icon">
                      <font-awesome-icon class="icono_animated" icon="eye" style="font-size:22px; color:var(--colorGris1)"/>
                    </div>
                </div>
              </router-link>
            </div>
            <div class="next" @click="avanzarOpcion()">
              <font-awesome-icon icon="arrow-down" style="font-size:22px; color:#967200"/>
            </div>
          </div>
        </div>

        <!-- Imagen -->
        <div class="me-img">
            <div class="me-img-container">
              <img src="@/assets/avatar1.png" alt="me-img" class="img">
            </div>
        </div>
      </div>
      <div class="components">
        <router-view/>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Me",
  data() {
    return {
      nombre: 'Enrique',
      opcion: 1,
      option1: true,
      option2: false,
      option3: false,
      option4: false,
      option5: false,
    }
  },
  computed: {
    cambiarFondo() {
      return {

        'btn-bg-about': this.option1,
        'btn-bg-services': this.option2,
        'btn-bg-projects': this.option3,
        'btn-bg-clients': this.option4,
        'btn-bg-contact': this.option5,

      }
    }
  },
  methods: {
    activarOpcion() {

        switch(this.opcion) {

          case 1:
            this.option1 = true;
            this.option2 = false;
            this.option3 = false;
            this.option4 = false;
            this.option5 = false;
            break;
          case 2:
            this.option2 = true;
            this.option1 = false;
            this.option3 = false;
            this.option4 = false;
            this.option5 = false;
            break;
          case 3:
            this.option3 = true;
            this.option2 = false;
            this.option1 = false;
            this.option4 = false;
            this.option5 = false;
            break;
          case 4:
            this.option4 = true;
            this.option2 = false;
            this.option3 = false;
            this.option1 = false;
            this.option5 = false;
            break;
          case 5:
            this.option5 = true;
            this.option2 = false;
            this.option3 = false;
            this.option4 = false;
            this.option1 = false;
            break;
          default:
            break;
        }
      },
      avanzarOpcion() {

        if(this.opcion == 5) {
          this.opcion = 1;
        } else {
          this.opcion ++;
        }
        this.activarOpcion();
      },

  },

}
</script>
<style>
