  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 15px 0 0 0;
  border-radius: 3px;
  /* background-image: linear-gradient(to top, #c4c5c7 0%, #dcdddf 52%, #ebebeb 100%); */
  box-shadow: 0 0 11px rgb(180, 180, 180, 1.7);
  color: var(--colorGris1);
  cursor: pointer;
  transition: all 0.3s ease;

}
.opcion:hover {
  box-shadow: 10px 10px 50px rgb(180, 180, 180, 3);
  transform: scale(1.05);
}

.icon {

  color: white;
  cursor: pointer;
}


.me-img {

