  /* animation: img 5s infinite; */
  /* border-radius: 50%; */
  /* box-shadow:  0 0 1px #fff; */
}
.img {

  width: 100%;
  height: 90%;
  border-radius: 10%;

}


/* Clases generales */

/* Clases del color del boton y los iconos */
.btn-color-black {
  color: white;
}
.btn-color-black {
  color: black;
}
/* Claes de los botones de indicacion */
.indicador-active {
  opacity: 1;
}
.indicador-no-active {
  opacity: 0.3;
}
/* Clases de aparición del servicio */
.option-active {
  display: flex;
  animation: aparicion 1s;
}
.option-no-active {
  display: none;
}


/* Clases de animaciones y transiciones */

@keyframes arriba_abajo{

  0% {
     transform: translateY(-10%);
   }
   50% {
     transform: translateY(10%);
   }
   100% {
     transform: translateY(-10%);
   }

}
@keyframes aparicion{

    from {
       opacity: 0;
       transform: translateY(-100%);
     }
    to {
      transform: translateY(0%);
       opacity: 1;
     }
}
@keyframes img {

  0% {
     transform: translateX(0%);
   }
   50% {
     transform: translateX(30%);
   }
   100% {
     transform: translateX(0%);
   }

}
@keyframes texto1 {

  0% {
     text-shadow: #967200 0.3px 0.3px;
   }
   50% {
     text-shadow: #967200 4px 4px;
   }
   100% {
     text-shadow: #967200 0.3px 0.3px;
   }
}


