        //
        // }

        const valid = this.$refs.form.validate();
        if (valid) {
          this.pasarFormulario();
          this.reset();
        }


      },
      reset () {
        this.$refs.form.reset()
      },
      pasarFormulario() {

        const nombre = this.name;
        const email = this.email;
        const asunto = this.subject;
        const option = this.select;
        const telefono = this.telefono;
        const resumen = this.area;

        console.log(nombre, email, asunto, option, telefono, resumen);

        const cliente = {
          name: nombre,
          email: email,
          telephone_number: telefono,
          subject: asunto,
          option_project: option,
          resume : resumen,

        }

        const path = `https://clientsportafolio.herokuapp.com/api/v1.0/crear/`;
            axios.post(path, cliente).then( resp => {

