import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret,
         faArrowDown,
         faEye,
         faCode,
         faMicrophone,
         faDatabase,
         faMobile,
         faChartArea,
         faLaptop,
         faDesktop,
         faEnvelope,
         faChalkboardTeacher} from '@fortawesome/free-solid-svg-icons'
import { faFacebookSquare,
        faInstagramSquare,
        faTwitterSquare,
        faLinkedin,
        faAws,
        faHtml5,
        faCss3,
        faVuejs,
        faJs,
        faPython,
        faJava,
        faWhatsapp,
        faGithub

        } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import vuetify from './plugins/vuetify';

library.add(faUserSecret,
    faArrowDown,
    faEye,
    faFacebookSquare,
    faInstagramSquare,
    faTwitterSquare,
    faLinkedin,
    faAws,
    faHtml5,
    faCss3,
    faVuejs,
    faJs,
    faPython,
    faJava,
    faCode,
    faMicrophone,
    faDatabase,
    faMobile,
    faChartArea,
    faLaptop,
    faDesktop,
    faChalkboardTeacher,
    faEnvelope,
    faWhatsapp,
    faGithub
)
Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  // boxicons,
  render: h => h(App)
}).$mount('#app')
